.header{
    width: 100%;
    background-color: #FFFFFF;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    position: fixed;
    top: 0;
    left: 0;
    min-height: 50px;
    z-index: 2;
}
/* HEADER SEARCH */
.header__search{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.header__search__img{
    width: 40px;
}
.header__search__barSearch{
    display: flex;
    flex-direction: column;
    position: relative;
    justify-content: center;
}
.header__search__barSearch__icon{
    position: absolute;
    align-self: flex-start;
    z-index: 1;
    transform: translateX(5px);
}
.header__search__barSearch__input{
    padding: 10px 10px 10px 30px;
    outline: none;
    border: none;
    background-color: #F0F2F5;
    border-radius: 25px;
}

/* HEADER SEARCH BOX */
.header__searchBox{
    position: absolute;
    left: 5px;
    top: 60px;
    width: 400px;
    background-color: #FFFFFF;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
}
.header__searchBox__noresult{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10px;
}
.header__searchBox__noresult *{
    /*margin-top: 20px !important;*/
}
.header__searchBox__noresult__text{
    font-size: 1.2rem;
    font-weight: bold;
}
.header__searchBox__friend{
    display: flex;
    flex-direction: row;
    align-items: center;
    align-self: flex-start;
    justify-content: space-between;
    width: 100%;
    padding-bottom: 10px;
    border-bottom: 1px solid #F0F2F5;
}
.header__searchBox__friend__information{
    display: flex;
    flex-direction: row;
    align-items: center;
    font-weight: bold;
}
.header__searchBox__friend__information__avatar{
    margin-right: 10px;
}

/* HEADER NAVIGATION */
.header__nav{
    padding: 5px 0;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}
.header__nav__btn{
    padding: 10px 30px !important;
    position: relative;
}
.header__nav__btn:hover{
    cursor: pointer;
    background-color: #F0F2F5;
}
.selected span{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 3px;
    background-color: #3F51B5;
}

/* HEADER ACCOUNT */
.header__account{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.header__account__user{
    display: flex;
    flex-direction: row;
    align-items: center;

}
.header__account__user__avatar{
    margin-right: 10px;
}
.header__account__btn{
    padding: 5px;
    border-radius: 50%;
    margin-left: 5px !important;
}
.header__account__btn:hover{
    cursor: pointer;
}
.header__account__btn__icon{
    pointer-events: none !important;
}

/* HEADER PLUS */
.header__plus{
    display: flex;
    position: absolute;
    background-color: #FFFFFF;
    border-radius: 5px;
    right: 20px;
    top: 110%;
    width: 200px;
    padding: 8px;
    flex-direction: column;
}
.header__plus__btn{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 10px 0;
}
.header__plus__btn:hover{
    background-color: #F0F2F5;
    cursor: pointer;
}
.header__plus__btn__icon{
    margin-right: 10px;
}
.header__plus__profile{
    border-bottom: 1px solid lightgrey;
}
.header__plus__profile__text{
    margin-left: 10px;
    display: flex;
    flex-direction: column;
}
.header__plus__profile__text__placeholder{
    font-weight: lighter;
    font-size: 0.8rem;
}

/* NOTIFICATION */
.header__notification{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #FFFFFF;
    position: absolute;
    top: 110%;
    right: 20px;
    width: 400px;
    padding: 8px;
}
.header__notification__noresult{
    font-weight: bold;
    font-size: 1rem;
}
.header__notification__requester{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding-bottom: 10px;
    border-bottom: 1px solid #F0F2F5;
}
.header__notification__requester__information{
    display: flex;
    flex-direction: row;
    align-items: center;
    font-weight: bold;
}
.header__notification__requester__information__avatar{
    margin-right: 10px !important;
}
.header__notification__requester__btnAccept{
    margin-right: 5px !important;
}
.header__notification__requester__btn{
    display: flex;
    align-items: center;
    flex-direction: row;
}

/* MEDIA QUERIES */
@media only screen and (max-width: 800px) {
    .header__search__barSearch{
        width: 100px;
    }
    .header__nav{
        flex: 1;
    }
    .header__account__user__text {
        display: none;
    }
    .header__nav__btn{
        padding: 10px 5px !important;
    }
    .header__account__btn{
        padding: 5px !important;
    }
}